<template>
  <div class="hs-container">
    <c-header isBackNav :title="Number($route.query.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')" fixedBackPath="/home">
      <p class="c-header-right" slot="right" v-if="Number($route.query.TravelType) === 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>
    <hs-scroll ref="tableView" :data="[details]" class="tableView">
      <ul>
        <div class="slider-box">
          <hs-swiper maxType="Width" :images="banners"></hs-swiper>
        </div>
        <div class="section">
          <template v-if="language === 'CN'">
            <div class="item-china">
              <div class="left" @click="[showWhereBack = false, showWhereGo = true]">
                <p>{{$t('base.DepartureCity')}}</p>
                <p :class="{placeholder: !trainOrder.DepartStationName}">{{trainOrder.DepartStationName ? trainOrder.DepartObject[`CityName_${language}`] : $t('base.PleaseChoose')}}</p>
              </div>
              <div class="logo" style="background-image: url('static/image/train/query/trainSwitch.png')" :style="{transform: `rotate(${360 * rotate}deg)`}"  @click="_exchangeWhere"></div>
              <div class="right" @click="[showWhereGo = false, showWhereBack = true]">
                <p>{{$t('base.ArrivalCity')}}</p>
                <p :class="{placeholder: !trainOrder.ArrivalStationName}">{{trainOrder.ArrivalStationName ? trainOrder.ArrivalObject[`CityName_${language}`] : $t('base.PleaseChoose')}}</p>
              </div>
            </div>
          </template>
          <template v-else>
            <div class="item-china" :type="language">
              <div class="right">
                <div class="from" @click="[showWhereBack = false, showWhereGo = true]">
                  <p>{{$t('base.DepartureCity')}}</p>
                  <div>
                    <p :class="{placeholder: !trainOrder.DepartStationName}">{{trainOrder.DepartStationName ? trainOrder.DepartObject.Code : $t('base.PleaseChoose')}}</p>
                    <p v-if="trainOrder.DepartStationName">{{trainOrder.DepartObject[`CityName_${language}`]}}</p>
                  </div>
                </div>
                <div class="to" @click="[showWhereGo = false, showWhereBack = true]">
                  <p>{{$t('base.ArrivalCity')}}</p>
                  <div>
                    <p :class="{placeholder: !trainOrder.ArrivalStationName}">{{trainOrder.ArrivalStationName ? trainOrder.ArrivalObject.Code : $t('base.PleaseChoose')}}</p>
                    <p v-if="trainOrder.ArrivalStationName">{{trainOrder.ArrivalObject[`CityName_${language}`]}}</p>
                  </div>
                </div>
              </div>
              <div class="left">
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/change.png')" :style="{transform: `rotate(${360 * rotate + 90}deg)`}" @click="_exchangeWhere"></div>
              </div>
            </div>
          </template>
          <div class="item-date">
            <div class="selected" @click="[showWhenBack = false, showWhenGo = true]">
              <p>{{$t(trainOrder.DepartDate, 'date', ['MM月dd日', 'MM dd'])}}</p>
              <p>{{getWeeks(trainOrder.DepartDate)}}<template v-if="language === 'CN'">出发</template></p>
            </div>
            <div class="selected" @click="[showWhenGo = false, showWhenBack = true]" v-if="trainOrder.ArrivalDate">
              <p>{{$t(trainOrder.ArrivalDate, 'date', ['MM月dd日', 'MM dd'])}}</p>
              <p>{{getWeeks(trainOrder.ArrivalDate)}}<template v-if="language === 'CN'">返回</template></p>
              <div class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')" @click.stop="_clearReturnDate"></div>
            </div>
            <div class="return" @click="[showWhenGo = false, showWhenBack = true]" v-else>
              <div class="logo" style="background-image: url('static/image/plane-ticket/query/date.png')"></div>
              <p>{{$t('base.ReturnDate')}}</p>
            </div>
          </div>
          <div class="item-isHighSpeed" @click="isHighSpeed=!isHighSpeed">
            <div class="logo" v-show="!isHighSpeed" style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
            <div class="logo" v-show="isHighSpeed" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
            <span>{{$t('base.JustLookAtTheHighSpeedTrain')}}</span>
          </div>
          <hs-gradient-button style="margin: 20px 0" :title="$t('base.Search')" @click="_gotoQuery"></hs-gradient-button>
        </div>
      </ul>
    </hs-scroll>
    <hs-address-picker v-model="showWhereGo" :switchList="[$t('base.City')]" :title="$t('base.DepartureCity')" @select="_setAddressWhereGo"></hs-address-picker>
    <hs-address-picker v-model="showWhereBack" :switchList="[$t('base.City')]" :title="$t('base.ArrivalCity')" @select="_setAddressWhereBack"></hs-address-picker>
    <hs-date-picker v-model="showWhenGo" :date="trainOrder.DepartDate" @changeDate="_setDateWhenGo" :maxToDataDays="details.CanBookDays" :title="$t('base.DepartureDate')"></hs-date-picker>
    <hs-date-picker v-model="showWhenBack"
      :date="_setInitArrivalDate()"
      @changeDate="_setDateWhenBack"
      :overdueDate="trainOrder.DepartDate"
      :maxToDataDays="details.CanBookDays"
      :title="$t('base.ReturnDate')"></hs-date-picker>
    <c-travel-standard v-model="showTravelStandard" :details="details.TrainRank" :travelPolicy="details.TravelPolicy" :type='3'></c-travel-standard>
  </div>
</template>
<script type='text/ecmascript-6'>
import HsScroll from 'base/hs-scroll/hs-scroll'
import CHeader from 'components/c-header/c-header'
import HsSwiper from 'base/hs-swiper/hs-swiper'
import HsAddressPicker from 'base/hs-address-picker/hs-address-picker'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsDatePicker from 'base/hs-date-picker/hs-date-picker'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import {kpiTrainGetHotelIndexInitInfo} from 'api'
import { mapGetters, mapMutations } from 'vuex'
export default {
  created() {
    /** 存TravelType, 若无则返回首页 */
    let res = {...this.trainOrder}
    res.TravelType = this.$route.query.TravelType
    this.SET_TRAIN_ORDER(res)
    if (this.noValue(this.trainOrder.TravelType)) {
      this.$router.replace('/home')
      return
    }
    /*****************************************************/
    this._readlocalStorageWithQuery()

    this._getTrainRight()
  },
  computed: {
    ...mapGetters([
      'trainOrder'
    ])
  },
  data() {
    return {
      // 轮播图数据
      banners: ['/static/image/train/query/banner_01.jpeg'],
      /** 差旅标准 */
      showTravelStandard: false,
      details: {},
      /********************************************************************/
      // 旋转切换动效参数
      rotate: 0,
      /** 去程返程参数 */
      showWhereGo: false,
      showWhereBack: false,
      showWhenGo: false,
      showWhenBack: false,
      /********************************************************************/
      /** 只看高铁动车 */
      isHighSpeed: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_TRAIN_ORDER'
    ]),
    /** 获取火车票差旅标准 */
    _getTrainRight() {
      kpiTrainGetHotelIndexInitInfo().then((res) => {
        console.log('机票权限查询页面：', res)
        this.details = res.ResultData
      })
    },
    /** 切换出发到达城市 */
    _exchangeWhere() {
      ++this.rotate
      let res = {...this.trainOrder}
      res.DepartObject = this.trainOrder.ArrivalObject
      res.ArrivalObject = this.trainOrder.DepartObject
      res.DepartStationCode = this.trainOrder.ArrivalStationCode
      res.ArrivalStationCode = this.trainOrder.DepartStationCode
      res.DepartStationName = this.trainOrder.ArrivalStationName
      res.ArrivalStationName = this.trainOrder.DepartStationName
      this.SET_TRAIN_ORDER(res)
    },
    /** 选择地址和日期 */
    // 去程
    _setAddressWhereGo(City) {
      let res = {...this.trainOrder}
      res.DepartObject = City
      res.DepartStationCode = City.CityCode
      res.DepartStationName = City[`CityName_${this.language}`]
      this.SET_TRAIN_ORDER(res)
    },
    // 返程
    _setAddressWhereBack(City) {
      let res = {...this.trainOrder}
      res.ArrivalObject = City
      res.ArrivalStationCode = City.CityCode
      res.ArrivalStationName = City[`CityName_${this.language}`]
      this.SET_TRAIN_ORDER(res)
    },
    // 去程时间
    _setDateWhenGo(date) {
      console.log('去程时间' + date)
      let res = {...this.trainOrder}
      res.DepartDate = date
      this.SET_TRAIN_ORDER(res)
      /** 如果选择的去程日期大于返程日期, 则自动修改返程日期为去程日期+1 */
      if (this.trainOrder.ArrivalDate && (this.trainOrder.DepartDate > this.trainOrder.ArrivalDate)) {
        this._setDateWhenBack(this.beTime(new Date(date).setDate(new Date(date).getDate() + 1)))
      }
      /*********************************************************************************************************************************************************/
    },
    // 回程时间
    _setDateWhenBack(date) {
      let res = {...this.trainOrder}
      res.ArrivalDate = date
      this.SET_TRAIN_ORDER(res)
      /** 回程时间大于最大回程时间时, 回程时间设置为最大回程时间 */
      let maxToData = this.beTime(new Date().setDate(new Date().getDate() + this.details.CanBookDays - 1))
      if (this.trainOrder.ArrivalDate && (this.trainOrder.ArrivalDate > maxToData)) {
        this._setDateWhenBack(maxToData)
      }
      /*********************************************************************************/
    },
    /** 初始化回程时间, 默认会去程时间的第二天. 若回程时间大于最大回程时间时, 回程时间设置为最大回程时间 */
    _setInitArrivalDate() {
      let dateAfterTomorrow = this.beTime(new Date(this.trainOrder.DepartDate).setDate(new Date(this.trainOrder.DepartDate).getDate() + 1))
      let maxToData = this.beTime(new Date().setDate(new Date().getDate() + this.details.CanBookDays - 1))
      if (this.trainOrder.ArrivalDate) {
        return this.trainOrder.ArrivalDate
      } else if (dateAfterTomorrow > maxToData) {
        return maxToData
      } else {
        return dateAfterTomorrow
      }
    },
    /*********************************************************************************/
    /** 清楚返程日期 */
    _clearReturnDate() {
      let res = {...this.trainOrder}
      res.ArrivalDate = ''
      this.SET_TRAIN_ORDER(res)
    },
    /** 火车票查询 */
    _gotoQuery() {
      if (this.noValue(this.trainOrder.DepartStationCode)) {
        this.$toast(this.$t('TM.PleaseSelectTheDepartureCity'))
        return
      } else if (this.noValue(this.trainOrder.ArrivalStationCode)) {
        this.$toast(this.$t('TM.PleaseSelectArrivalCity'))
        return
      } else if (this.trainOrder.DepartStationName === this.trainOrder.ArrivalStationName) {
        this.$toast(this.$t('TM.DepartureCityAndArrivalCityCannotBeSame'))
        return
      }
      let res = {...this.trainOrder}
      res.isHighSpeed = this.isHighSpeed
      this.SET_TRAIN_ORDER(res)
      let isOneWay = !(this.trainOrder.ArrivalDate)
      this.$router.push({
        path: '/train/trainQueryList',
        query: {
          isOneWay: isOneWay,
          orderID: this.$route.query.orderID,
          TrainCode: this.$route.query.TrainCode,
          segment: this.$route.query.segment
        }
      })

      this._savelocalStorageWithQuery()
    },

    /** 读写localStorage内的已选项目 */
    _savelocalStorageWithQuery() {
      window.localStorage.HISTORY_QUERY_TRAIN = JSON.stringify({
        goWhere: this.trainOrder.DepartObject,
        backWhere: this.trainOrder.ArrivalObject,
        goWhen: this.trainOrder.DepartDate,
        backWhen: this.trainOrder.ArrivalDate
      })
    },
    _readlocalStorageWithQuery() {
      if (window.localStorage.HISTORY_QUERY_TRAIN) {
        let res = JSON.parse(window.localStorage.HISTORY_QUERY_TRAIN)
        this._setAddressWhereGo(res.goWhere)
        this._setAddressWhereBack(res.backWhere)
        this._setDateWhenGo(res.goWhen)
        if (res.backWhen && res.backWhen > this.trainOrder.DepartDate) {
          this._setDateWhenBack(res.backWhen)
        }
      }
    }
    /*********************************************************************************************************/
  },
  components: {
    HsScroll,
    CHeader,
    HsSwiper,
    HsAddressPicker,
    HsGradientButton,
    HsDatePicker,
    CTravelStandard
  }
}
</script>
<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-container
  width 100%
  height 100%
  .c-head-right
    font-size 0.9rem
    color $color_black_text_0
  >.tableView
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .slider-box
      width 100%
      height 120px
      overflow hidden
    .section
      width calc(100% - 20px)
      margin-left 10px
      background-color $color_white
      border-radius 5px
      border 1px solid $color_gray_layer
      padding 5px 10px
      margin-top 10px
      >.item-china
        @extend .flex-row
        @extend .flex-between
        width 100%
        border-bottom 1px solid $color_gray_layer
        >.left
        >.right
          @extend .flex-column
          @extend .flex-center
          width calc(50% - 20px)
          height 70px
          >p
            width 100%
            &:nth-child(1)
              font-size 0.8rem
              color $color_gray_text
              margin-bottom 10px
            &:nth-child(2)
              font-size 1.5rem
              color $color_black_text_0
              font-weight bold
              height 1em
          // >.placeholder
          //   color $color_gray_text !important
          //   font-weight normal !important
        >.logo
          width 40px
          height 40px
          background-size 100% 100%
          transition all .4s
        >.left
          >p
            text-align left
        >.right
          >p
            text-align right
      >.item-china[type="EN"]
        @extend .flex-row
        @extend .flex-between
        width 100%
        height 155px
        >.right
          width calc(100% - 40px)
          >.from
          >.to
            width 100%
            padding 10px 0
            >p
              width 100%
              font-size 0.8rem
              color $color_gray_text
              margin-bottom 5px
            >div
              @extend .flex-column
              @extend .flex-center
              @extend .flex-top
              width 100%
              height 40px
              >p
                width 100%
                &:nth-child(1)
                  font-size 1.5rem
                &:nth-child(2)
                  font-size 0.8rem
                  color $color_black_text_0
                  margin-top 2px
          >.from
            border-bottom 1px solid $color_gray_layer
        >.left
          @extend .flex-center
          width 30px
          height 100%
          >.logo
            width 30px
            height 30px
            background-size 100% 100%
            transition all .4s
      >.item-date
        @extend .flex-row
        @extend .flex-between
        width 100%
        border-bottom 1px solid $color_gray_layer
        >.selected
        >.return
          @extend .flex-row
          width 50%
          height 60px
        >.selected
          @extend .flex-start
          position relative
          >p
            &:nth-child(1)
              font-size 1rem
              font-weight bold
            &:nth-child(2)
              font-size 0.7rem
              margin-left 2px
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            position absolute
            top 22px
            right 0px
        >.return
          @extend .flex-end
          color $color_gray_text
          font-size 1rem
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            margin-right 6px
      >.item-isHighSpeed
        @extend .flex-row
        @extend .flex-start
        width 100%
        height 60px
        border-bottom 1px solid $color_gray_layer
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
        >span
          margin-left 10px
</style>



